<template>
  <div class="hello">
  	<div class="body_box">
   <!---->
   	<div class="head_box">
   		<img src="../../static/app_png/logo.png"/>
   		<div class="head_box_right">
   			<i class="iconfont icon-sousuo"></i>
   			<input type="text" placeholder="" />
   			<i class="icon-xiazai16 iconfont"></i>
   		</div>
   	</div>
   	<!--tab-->
   	<div class="container"> 
		<div class="swiper-container swiper1"> 
				<div class="swiper-wrapper" style="display: flex;flex-direction:column"> 
					<div class="swiper-slide selected fnav"></div> 
					<div class="swiper-slide fnav"></div> 
					<div class="swiper-slide fnav"></div> 
					<div class="swiper-slide fnav"></div> 
					<div class="swiper-slide fnav"></div> 
					<div class="swiper-slide fnav"></div> 
					<div class="swiper-slide fnav"></div> 
					
	            </div> 
	    </div>
	    <div class="swiper-container swiper2"> 
				<div class="swiper-wrapper"> 
					<div class="swiper-slide swiper-no-swiping">
						<div class="contentbox contentbox1" v-for="i in contentbox1">
							<div @click="aa(i)"><img :src="i.imageurl"/></div>
							<p>{{i.name}}</p>
						</div>
					</div> 
					<div class="swiper-slide swiper-no-swiping" >
						<div class="contentbox contentbox2" v-for="i in contentbox2">
							<div @click="aa(i)"><img :src="i.imageurl"/></div>
							<p>{{i.name}}</p>
						</div>
					</div> 
					<div class="swiper-slide swiper-no-swiping" >
						<div class="contentbox contentbox3" v-for="i in contentbox3">
							<div @click="aa(i)"><img :src="i.imageurl"/></div>
							<p>{{i.name}}</p>
						</div>
					</div> 
					<div class="swiper-slide swiper-no-swiping" >
						<div class="contentbox contentbox4" v-for="i in contentbox4">
							<div @click="aa(i)"><img :src="i.imageurl"/></div>
							<p>{{i.name}}</p>
						</div>
					</div> 
					<div class="swiper-slide swiper-no-swiping" >
						<div class="contentbox contentbox5" v-for="i in contentbox5">
							<div @click="aa(i)"><img :src="i.imageurl"/></div>
							<p>{{i.name}}</p>
						</div>
					</div> 
					<div class="swiper-slide swiper-no-swiping" >
						<div class="contentbox contentbox6" v-for="i in contentbox6">
							<div @click="aa(i)"><img :src="i.imageurl"/></div>
							<p>{{i.name}}</p>
						</div>
					</div> 
					<div class="swiper-slide swiper-no-swiping" >
						<div class="contentbox contentbox7" v-for="i in contentbox7">
							<div @click="aa(i)"><img :src="i.imageurl"/></div>
							<p>{{i.name}}</p>
						</div>
					</div> 
					
			    </div> 
			  </div> 
		</div> 
		<!---->
		
	</div>
  </div>
</template>

<script>
	
	import jQuery from '../../static/jquery-3.2.1.min.js'
	import Swiper from '../../static/swiper'
	import store from '../vuex/carstore'
export default {
  store,
  components:{},
  data () {
    return {
       contentbox1:null,
       contentbox2:null,
       contentbox3:null,
       contentbox4:null,
       contentbox5:null,
       contentbox6:null,
       contentbox7:null
    }
  },
  mounted:function(){
  	$(function() { 
function setCurrentSlide(ele, index) { 
$(".swiper1 .swiper-slide").removeClass("selected"); 
ele.addClass("selected"); 
//swiper1.initialSlide=index; 
} 
var swiper1 = new Swiper('.swiper1', { 
//	sliderslides(carousel) 
//	number 'auto'slides 
//	loop'auto'loopedSlides 
slidesPerView: 5.5, 
paginationClickable: false,//trueSwiper 
spaceBetween:0,//slidepx 
freeMode: true,//falseslidewrappertruefreeslide 
loop: false,// 
onTab: function(swiper) { 
var n = swiper1.clickedIndex; 
} 
}); 
swiper1.slides.each(function(index, val) { 
var ele = $(this); 
ele.on("click", function() { 
setCurrentSlide(ele, index); 
swiper2.slideTo(index, 500, false); 
//mySwiper.initialSlide=index; 
}); 
}); 
var swiper2 = new Swiper('.swiper2', { 
//freeModeSticky true  
direction: 'horizontal',//Slides(horizontal)(vertical) 
loop: false, 
//	effect : 'fade',// 
//effect : 'cube',// 
//effect : 'coverflow',//3D 
//	effect : 'flip',//3D 
autoHeight: true,//truewrappercontainerslide 
onSlideChangeEnd: function(swiper) { //swiperslideslide 
var n = swiper.activeIndex; 
setCurrentSlide($(".swiper1 .swiper-slide").eq(n), n); 
swiper1.slideTo(n, 500, false); 
} 
}); 
});
     //   
    
			var that=this;
			this.$axios({
				url:'../../static/json/classify.json',
				method:'get'
				
			}).then(function(res){
				console.log(res)
				console.log(res.data[0].childCategoryList)
				that.contentbox1=res.data[0].childCategoryList
				that.contentbox2=res.data[1].childCategoryList
				that.contentbox3=res.data[2].childCategoryList
				that.contentbox4=res.data[3].childCategoryList
				that.contentbox5=res.data[4].childCategoryList
				that.contentbox6=res.data[5].childCategoryList
				that.contentbox7=res.data[6].childCategoryList
			})
		
  }
  ,
  methods:{
        aa(i){
				console.log(i)
//				window.location.href='/#/cart?'+i.pid
				//vuex
				this.$store.state.getList(i.categoryId);
				window.location.href='/#/cart'
			}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	
	@import url("../../static/swiper.css");
	.body_box{
		background: #f4f4f4;
		overflow: hidden;
		height: 1900px;
		/*margin-bottom: 144px;*/
	}
  .head_box{
		height: 145px;
		background: #FFFFFF;
		line-height: 145px;
		border-bottom: 1px solid #ccc;
	}
	.head_box img{
		width: 142px;height: 90px;float: left;margin-top: 30px;margin-right: 40px;margin-left: 40px;
	}
	.head_box_right{
		overflow: hidden;
	}
	.head_box input{
		height: 90px;
		width: 718px;
		float: left;
		padding-left: 200px;
		box-sizing: border-box;
		border-radius:40px ;
		margin-top: 30px;
	}
	.icon-xiazai16{
		font-size: 70px;
		float: left;
		margin-left: 40px;
	}
	.icon-sousuo{
		font-size: 48px;
		margin-left: -680px;
		
	}
	/*tab*/
	.container{
		clear: both;
	}
	.selected{
		color: red;
	}
	
	.fnav{
		width: 270px !important;
		height: 140px ;
		line-height: 140px;
		margin: 10px 0;
		background: #FFFFFF;
		text-align: center;
	}
	.swiper1{
		width: 270px !important; 
		height: auto;
		margin-top: 32px;
		float: left;
	}
	.swiper2{
		width: 750px ;float: left;margin-left: 40px;margin-top: 32px;background: #fff;
		height: auto;
	}
	.swiper2 .swiper-slide{
		width: 750px !important;float: left;
	}
	/**/
	.contentbox{
		height: 350px;width: 250px;float: left;
	}
	.contentbox img{
		width:250px ;height: 225px;
	}
	.contentbox p{
		text-align: center;height: 70px;line-height: 70px;
	}
</style>



