<template>
	<div>
		<div class="body_box">
			<div class="top">
				<p @click="return1"><</p>
				<span></span>
			</div>
			<div class="top_bottom">
				<img src="../../static/app_png/logo.png"  />
				<div class="txt">
					<p class="top_bottom1"></p>
					<p class="top_bottom2"></p>
				</div>
				<button @click="return2"></button>
			</div>
			<!---->
			<!---->
			<div >
				<div class="swiper-container" >
				  <div class="swiper-wrapper">
				    <div class="swiper-slide"><img :src='arr.thumbImg'/></div>
				  </div>
	            </div>
	            <p class="p1">{{arr.goodsName}}</p>
	            <p>{{arr.salePrice}}</p>
            </div>
			<!--E-->
		</div>
	</div>
</template>

<script>
	import jQuery from '../../static/jquery-3.2.1.min.js'
	import Swiper from '../../static/swiper'
	import store from '../vuex/carstore'
	export default{
		store,
		data(){
			return{
				arr:{
					thumbImg:'',
					salePrice:''
				}
				
			}
		},
		methods:{
			return1:function(){
				window.history.back(-1)
				this.$store.commit("abc",'flex')
			},
			return2:function(){
				window.location.href='/#/home'
				this.$store.commit("abc",'flex')
			}
		},
		mounted(){
			this.$store.commit("abc",'none')
			var mySwiper = new Swiper('.swiper-container', {
					autoplay: 5000,//
				})
			//
//			console.log(localStorage.getItem('pid'))
			
		      this.arr.goodsName=localStorage.getItem('goodsName')
		      
            this.arr.salePrice=localStorage.getItem('salePrice')
            this.arr.thumbImg=localStorage.getItem('thumbImg')	
            	console.log(this.arr.thumbImg)	
            	 
            }
			
		
	}
</script>

<style scoped>
	@import url("../../static/swiper.css");
	.body_box{
		width: 1080px;
		background: #fff;
	}
	.top{
		position: relative;
		width: 1080px;
		text-align: center;
		height: 148px;
		line-height: 148px;font-size: 60px;
		border-bottom: 5px solid #ccc;
		
	}
	.top p{
		position: absolute;
		top: 0;left: 48px;font-size: 70px;
	}
	.top_bottom{
		width: 1080px;
		height: 210px;
	}
	.top_bottom img{
		width: 115px;
		height: 65px;
		float: left;
		margin-left: 40px;
		margin-right: 50px;
		margin-top: 50px;
	}
	.txt{
		float: left;margin-top: 30px;overflow: hidden;
	}
	.top_bottom button{
		float: right;
		width: 230px;
		height: 85px;
		margin-right: 16px;
		margin-top: 50px;
		color: #cb4a36;
		background: #fff;
		border: 5px solid #CB4A36;
	}
	
	.top_bottom1{
		font-size: 50px;
		color: #9c9c9c;
		
	}
	.top_bottom2{
		font-size: 50px;
		color: #ee6756;
		margin-top: 10px;
	}
	/**/
	.swiper-container{
		width: 1080px;
		height: 1000px;
		
	}
	.swiper-wrapper{
		width: 1080px;
		height: 1000px;
		display: flex;
		flex-direction: row;
		flex-wrap:nowrap
	}
	.swiper-slide{
		width: 1080px;
		height: 1000px;
		background: red;
	}
	.swiper-slide img{
		width: 1080px;
		height: 1000px;
	}
	.p1{
		font-size: 45px;
	}
</style>


