<template>
  <div class="hello">
  	<div class="body_box">
   <!---->
   	<div class="head_box">
   		<img src="../../static/app_png/logo.png"/>
   		<div class="head_box_right">
   			<i class="iconfont icon-sousuo"></i>
   			<input type="text" placeholder="" />
   			<i class="icon-xiazai16 iconfont"></i>
   		</div>
   	</div>
   	<!--tab-->
   	<div class="container"> 
			<div class="swiper-container swiper1"> 
				<div class="swiper-wrapper"> 
					<div class="swiper-slide selected"></div> 
					<div class="swiper-slide"></div> 
					<div class="swiper-slide"></div> 
					<div class="swiper-slide"></div> 
					<div class="swiper-slide"></div> 
					<div class="swiper-slide"></div> 
					<div class="swiper-slide"></div> 
					<div class="swiper-slide"></div> 
					
	      </div> 
	    </div>
	    <div class="swiper-container swiper2"> 
				<div class="swiper-wrapper"> 
					  <div class="swiper-slide swiper-no-swiping"><home1></home1></div> 
						<div class="swiper-slide swiper-no-swiping"><home2></home2></div> 
						<div class="swiper-slide swiper-no-swiping"><home3></home3></div> 
						<div class="swiper-slide swiper-no-swiping"><home4></home4></div> 
						<div class="swiper-slide swiper-no-swiping"><home5></home5></div> 
						<div class="swiper-slide swiper-no-swiping"><home6></home6></div> 
						<div class="swiper-slide swiper-no-swiping"><home7></home7></div> 
						<div class="swiper-slide swiper-no-swiping"><home8></home8></div> 
					
			    </div> 
			  </div> 
		</div> 
		<!---->
		<div class=""></div>
	</div>
  </div>
</template>

<script>
	import home1 from './hometab/home_one'
	import home2 from './hometab/home_two'
	import home3 from './hometab/home3'
	import home4 from './hometab/home4'
	import home5 from './hometab/home5'
	import home6 from './hometab/home6'
	import home7 from './hometab/home7'
	import home8 from './hometab/home8'
	import jQuery from '../../static/jquery-3.2.1.min.js'
	import Swiper from '../../static/swiper'

export default {
 
  components:{home1,home2,home3,home4,home5,home6,home7,home8},
  data () {
    return {
       
    }
  },
  mounted:function(){
  	$(function() { 
function setCurrentSlide(ele, index) { 
$(".swiper1 .swiper-slide").removeClass("selected"); 
ele.addClass("selected"); 
//swiper1.initialSlide=index; 
} 
var swiper1 = new Swiper('.swiper1', { 
//	sliderslides(carousel) 
//	number 'auto'slides 
//	loop'auto'loopedSlides 
slidesPerView: 5.5, 
paginationClickable: true,//trueSwiper 
spaceBetween: 0,//slidepx 
freeMode: true,//falseslidewrappertruefreeslide 
loop: false,// 
onTab: function(swiper) { 
var n = swiper1.clickedIndex; 
} 
}); 
swiper1.slides.each(function(index, val) { 
var ele = $(this); 
ele.on("click", function() { 
setCurrentSlide(ele, index); 
swiper2.slideTo(index, 500, false); 
//mySwiper.initialSlide=index; 
}); 
}); 
var swiper2 = new Swiper('.swiper2', { 
//freeModeSticky true  
direction: 'horizontal',//Slides(horizontal)(vertical) 
loop: false, 
//	effect : 'fade',// 
//effect : 'cube',// 
//effect : 'coverflow',//3D 
//	effect : 'flip',//3D 
autoHeight: true,//truewrappercontainerslide 
onSlideChangeEnd: function(swiper) { //swiperslideslide 
var n = swiper.activeIndex; 
setCurrentSlide($(".swiper1 .swiper-slide").eq(n), n); 
swiper1.slideTo(n, 500, false); 
} 
}); 
});
  }
  ,
  methods:{
  	
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	
	@import url("../../static/swiper.css");
	.body_box{
		background: #f4f4f4;
		overflow: hidden;
	}
  .head_box{
		height: 145px;
		background: #FFFFFF;
		line-height: 145px;
		border-bottom: 1px solid #ccc;
	}
	.head_box img{
		width: 142px;height: 90px;float: left;margin-top: 30px;margin-right: 40px;margin-left: 40px;
	}
	.head_box_right{
		overflow: hidden;
	}
	.head_box input{
		height: 90px;
		width: 718px;
		float: left;
		padding-left: 200px;
		box-sizing: border-box;
		border-radius:40px ;
		margin-top: 30px;
	}
	.icon-xiazai16{
		font-size: 70px;
		float: left;
		margin-left: 40px;
	}
	.icon-sousuo{
		font-size: 48px;
		margin-left: -680px;
		
	}
	/*tab*/
	.selected{
		color: red;text-decoration: underline;
	}
	.container{
		width: 100%;
		overflow: hidden;
	}
	.swiper1{
		width: 2300px !important; 
		background: #FFFFFF;
		line-height: 124px;
		height: 124px;
	}
	.swiper2 .swiper-slide{
		width: 1080px !important;
	}
	.swiper-slide{
		width: 270px !important;text-align: center;
	}
	.swiper2 img{
		width: 1080px;height: 430px;
	}
</style>



